import React from 'react'
import { Box, Image, Flex } from 'rebass'
import { StaticQuery, graphql } from 'gatsby'
import styled from 'styled-components'
import Fade from 'react-reveal/Fade'
import Section from '../components/Section'
import Wave from '../components/Wave'


const Usage = () => (
  <Section.Container id='usage'>
    <Section.Header name='Usage' />
    <StaticQuery
      query={graphql`
        query UsageQuery {
          contentfulSection(name: { eq: "Usage" }) {
            name
            contents {
              childMarkdownRemark {
                html
              }
            }
          }
        }
      `}
      render={data => {
        const { contents, profile } = data.contentfulSection;
        return (
          <Flex justifyContent='center' alignItems='center' flexWrap='wrap'>
            <Box width={[1, 1, 1]} px={[1, 2, 2]}>
              <Fade bottom>
                <div className='text-content' dangerouslySetInnerHTML={{ __html: contents.childMarkdownRemark.html }}></div>
              </Fade>
            </Box>
            <Wave direction='left'/>
          </Flex>
        );
      }}
    />
  </Section.Container>
)

export default Usage
