import React from 'react'
// import { Box, Image, Flex } from 'rebass'
import { StaticQuery, graphql } from 'gatsby'
import Image from 'gatsby-image'
import tw from 'tailwind.macro'
import styled from 'styled-components'
import ReactMarkdown from 'react-markdown'
import Fade from 'react-reveal/Fade'
import Section from '../components/Section'
import markdownRenderer from '../components/MarkdownRenderer'
import Wave from '../components/Wave'
import Logo from '../components/Logo/icon.png'

const Wrapper = styled.div`
  ${tw`flex justify-start items-start flex-wrap`}
  z-index: 9;
`

const Contents = styled.div`
  ${tw`flex w-3/5 justify-start items-start flex-wrap`}
`

const ImgWrapper = styled.div`
  ${tw`flex w-1/3 justify-end items-end flex-wrap mt-0 mx-auto mr-0`};
`

const LogoImg = styled.img`
  ${tw`relative w-full mx-auto mr-0`}
  top: -3.5rem;
  border-radius: 50%;
  transition: all 0.25s ease-out;
  &:hover {
    border-radius: 20%;
  }
`

const About = () => (
  <Section.Container id='about' className='first'>
    <Section.Header name='Motivation' />
    <StaticQuery
      query={graphql`
        query AboutMeQuery {
          contentfulAbout {
            aboutMe {
              childMarkdownRemark {
                html
              }
            }
            profile {
              fixed(resizingBehavior: SCALE) {
                ...GatsbyContentfulFixed_withWebp
              }
            }
          }
        }
      `}
      render={data => {
        const { aboutMe, profile } = data.contentfulAbout
        return (
          <Wrapper>
            <Contents>
              <Fade bottom>
                <div className='text-content' dangerouslySetInnerHTML={{ __html: aboutMe.childMarkdownRemark.html }}></div>
              </Fade>
            </Contents>

            <ImgWrapper>
              <Fade right>
                <LogoImg
                  src={Logo}
                  alt='logo'
                />
              </Fade>
            </ImgWrapper>
            <Wave />
          </Wrapper>
        )
      }}
    />
  </Section.Container>
)

export default About
