import React from 'react'
import { Box, Image, Flex } from 'rebass'
import { StaticQuery, graphql } from 'gatsby'
import styled from 'styled-components'
import Fade from 'react-reveal/Fade'
import Section from '../components/Section'

const Content = styled(Box)`
  .text-content {
    ol li {
      margin: 0.5rem 0;
    }
  }
`

const Contribute = () => (
  <Section.Container id='contribute'>
    <Section.Header name='Contribute' />
    <StaticQuery
      query={graphql`
        query ContributeQuery {
          contentfulSection(name: { eq: "Contribute" }) {
            name
            contents {
              childMarkdownRemark {
                html
              }
            }
          }
        }
      `}
      render={data => {
        const { contents, profile } = data.contentfulSection;
        return (
          <Flex justifyContent='center' alignItems='center' flexWrap='wrap'>
            <Content width={[1, 1, 1]} px={[1, 2, 2]}>
              <Fade bottom>
                <div className='text-content' dangerouslySetInnerHTML={{ __html: contents.childMarkdownRemark.html }}></div>
              </Fade>
            </Content>
          </Flex>
        );
      }}
    />
  </Section.Container>
)

export default Contribute
